<template>
  <div class="activityList">
    <div
      class="item"
      v-for="(item, i) in list"
      :key="i"
      @click="subscribe(item)"
    >
      <div>报刊名称：{{ item.hdmc }}</div>
      <div>
        <span>所属年度：{{ item.ssnd }}</span>
        <span class="right">
          <span :class="item.isBm === 0 ? 'red' : 'green'">{{
            item.isBm === 0 ? "未订阅" : "已订阅"
          }}</span>
          <van-icon name="arrow" />
        </span>
      </div>
      <div>订阅时间：{{ item.kssj + '-' + item.jssj }}</div>
    </div>
  </div>
</template>

<script>
import {dyjhQuerydata4list} from '../assets/js/api';
export default {
  data() {
    return {
      list: [],
      dspToken: localStorage.dspToken
    };
  },
  mounted() {
    dyjhQuerydata4list({
      dspToken: this.dspToken
    }).then(res => {
      this.list = res.data;
    });
  },
  methods: {
    subscribe(item) {
      this.$router.push({
        name: 'NewspaperSubscribe',
        query: {
          id: item.dyjhId
        }
      });
    }
  }
};
</script>

<style lang="less" scoped>
.activityList {
  .item {
    width: 704px;
    height: 195px;
    background-color: #ffffff;
    box-shadow: 3px 4px 9px 1px rgba(1, 88, 155, 0.05);
    border-radius: 8px;
    margin: 0 auto;
    margin-top: 23px;
    position: relative;
    padding: 31px 30px 26px 33px;
    box-sizing: border-box;
    &::before {
      content: "";
      width: 5px;
      height: 195px;
      background-color: #3198ed;
      position: absolute;
      left: 0;
      top: 0;
      border-top-left-radius: 50px;
      border-bottom-left-radius: 50px;
    }
    > div:first-child {
      font-size: 34px;
      color: #333333;
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
    }
    > div:nth-child(2) {
      font-size: 30px;
      color: #888888;
      margin-top: 29px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      height: 30px;
    }
    > div:last-child {
      font-size: 30px;
      color: #888888;
      margin-top: 4px;
    }
    .red {
      color: #d42a2b;
      font-size: 36px;
      margin-right: 10px;
    }
    .green {
      color: #2ad446;
      font-size: 36px;
      margin-right: 10px;
    }
  }
}
</style>



